html {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home {
  background-color: black;
  display: table;
}

.home-element {
  display: table-cell;
  position: absolute;
  vertical-align: middle;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}

h1,
h2,
h3 {

  margin: 0.4em 0;
  line-height: 1.2;
}

h4 {
  font-family: 'FiraCode-retina';
  margin: 0.2em 0;
  line-height: 1.2;
  letter-spacing: 0em;
}

h1 {
  font-size: 2.2em;
  color: #1db0ff;

}

h2 {
  font-size: 1.5em;
  color: #1db0ff;
}

h3 {
  font-size: 1.1em;
  margin: 2em 0 1em;
  font-weight: 100;
  color: #9cdcfe;
}

h4 {
  font-size: 0.9em;
  margin: 2em 0 0.5em;
  color: #caecb7;
}

p {
  margin: 1.5em 0;
  line-height: 1.4;
  letter-spacing: -0.05em;
  text-align: justify;
}

a {
  color: #ff9df7;
}

ul {
  margin: 1em 2em;
  padding: 0;
}

ul li {
  margin: 0;
  padding: 0.125em 0;
  color: rgb(224, 242, 255);
}

ul li ul {
  margin: 0.25em 1.25em;
}